.App {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  text-align: center;
  padding-top: 80px; /* Add padding to avoid overlap with Navbar */
  padding-bottom: 50px; /* Add padding to avoid overlap with Footer */
}

.content {
  position: relative;
  z-index: 2; /* Ensure it is above the Pattern component */
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 130px); /* Adjust height to account for Navbar and Footer */
  width: 100%;
}

.file-upload-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #444;
  border-radius: 10px;
  background-color: rgba(34, 34, 34, 0.8); /* Dark background to match the pattern */
  color: #f1f1f1; /* Light text color */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

button {
  margin-top: 10px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

input[type="file"] {
  margin: 20px 0;
}

.response {
  margin-top: 20px;
  padding: 10px;
  background-color: #f9f9f9;
  border-radius: 5px;
  border: 1px solid #ccc;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: auto;
  max-height: 300px;
}